.diceRoller{background-color:var(--card-background);border-radius:var(--radius);box-shadow:var(--shadow);padding:1.5rem;margin-bottom:1.5rem}.diceRoller h3{margin:0 0 1rem 0;color:var(--text-color);font-size:1.25rem;font-weight:600}.diceRoller .diceForm{display:flex;flex-direction:column;gap:1rem}.diceRoller .diceForm .presets{display:flex;flex-wrap:wrap;gap:.5rem}.diceRoller .diceForm .presets .presetButton{background-color:var(--background-color);color:var(--text-color);border:1px solid var(--border-color);border-radius:var(--radius);padding:.5rem;font-weight:500;flex:1;min-width:2.5rem;transition:all .2s ease}.diceRoller .diceForm .presets .presetButton:hover{background-color:var(--accent-color);color:#fff;border-color:var(--accent-color)}.diceRoller .diceForm .presets .presetButton:focus{outline:none;box-shadow:0 0 0 2px rgba(136,192,208,.5)}.diceRoller .diceForm .inputRow{display:flex;gap:.75rem}@media(max-width: 768px){.diceRoller .diceForm .inputRow{flex-direction:column}}.diceRoller .diceForm .inputRow .expressionInput{flex:1;padding:.75rem;border:1px solid var(--border-color);border-radius:var(--radius);font-size:1rem}.diceRoller .diceForm .inputRow .expressionInput:focus{outline:none;border-color:var(--accent-color);box-shadow:0 0 0 2px rgba(136,192,208,.3)}.diceRoller .diceForm .inputRow .characterSelect{min-width:150px;padding:.75rem;border:1px solid var(--border-color);border-radius:var(--radius);background-color:#fff;font-size:1rem}.diceRoller .diceForm .inputRow .characterSelect:focus{outline:none;border-color:var(--accent-color);box-shadow:0 0 0 2px rgba(136,192,208,.3)}.diceRoller .diceForm .inputRow .rollButton{background-color:var(--primary-color);color:#fff;border:none;border-radius:var(--radius);padding:.75rem 1.5rem;font-weight:600;text-transform:uppercase;font-size:.875rem;letter-spacing:.025em;transition:background-color .2s ease,transform .1s ease}.diceRoller .diceForm .inputRow .rollButton:hover{background-color:var(--secondary-color)}.diceRoller .diceForm .inputRow .rollButton:active{transform:translateY(1px)}.diceRoller .diceForm .inputRow .rollButton:disabled{background-color:var(--text-lighter);cursor:not-allowed}.diceRoller .diceHelp{margin-top:1rem;font-size:.875rem;color:var(--text-light);line-height:1.4}.diceRoller .diceHelp p{margin:.25rem 0}@keyframes rollAnimation{0%{transform:translateY(0) rotate(0);opacity:1}10%{transform:translateY(-10px) rotate(-5deg)}20%{transform:translateY(-15px) rotate(5deg)}30%{transform:translateY(-15px) rotate(-5deg)}40%{transform:translateY(-10px) rotate(5deg)}50%{transform:translateY(-5px) rotate(-3deg)}60%{transform:translateY(-2px) rotate(3deg)}70%{transform:translateY(0) rotate(-2deg)}80%{transform:translateY(0) rotate(2deg)}90%{transform:translateY(0) rotate(-1deg)}100%{transform:translateY(0) rotate(0);opacity:1}}.diceResult{background-color:var(--background-color);border-radius:var(--radius);padding:1rem;margin-top:1rem;display:flex;justify-content:space-between;align-items:center;animation:rollAnimation .6s ease-out}.diceResult .resultExpression{font-family:monospace;font-size:1.1rem;color:var(--text-color)}.diceResult .resultTotal{font-size:1.5rem;font-weight:700;color:var(--primary-color);background-color:#fff;padding:.5rem 1rem;border-radius:var(--radius);box-shadow:0 2px 4px rgba(0,0,0,.1)}.rollHistory{margin-top:1.5rem}.rollHistory .historyTitle{font-size:1.125rem;margin-bottom:1rem;color:var(--text-color);font-weight:600;display:flex;justify-content:space-between;align-items:center}.rollHistory .historyTitle .clearButton{font-size:.875rem;color:var(--text-light);background:none;border:none;text-decoration:underline}.rollHistory .historyTitle .clearButton:hover{color:var(--error-color)}.rollHistory .rollsList{margin:0;padding:0;list-style:none}.rollHistory .rollsList .rollItem{display:flex;justify-content:space-between;align-items:center;padding:.75rem;border-bottom:1px solid var(--border-color)}.rollHistory .rollsList .rollItem:last-child{border-bottom:none}.rollHistory .rollsList .rollItem .rollInfo{display:flex;flex-direction:column}.rollHistory .rollsList .rollItem .rollInfo .rollExpression{font-family:monospace;font-size:1rem}.rollHistory .rollsList .rollItem .rollInfo .rollMeta{font-size:.75rem;color:var(--text-lighter);margin-top:.25rem}.rollHistory .rollsList .rollItem .rollInfo .rollMeta .characterName{font-weight:500;color:var(--text-light)}.rollHistory .rollsList .rollItem .rollValue{font-size:1.25rem;font-weight:700;color:var(--primary-color)}.rollHistory .rollsList .rollItem.critical-success .rollValue{color:var(--success-color)}.rollHistory .rollsList .rollItem.critical-failure .rollValue{color:var(--error-color)}.rollHistory .emptyHistory{text-align:center;padding:2rem 0;color:var(--text-lighter);font-style:italic}.decorativeDice{position:absolute;width:40px;height:40px;opacity:.2;z-index:-1}.decorativeDice.d4{top:10%;left:10%;transform:rotate(15deg)}.decorativeDice.d6{top:20%;right:15%;transform:rotate(-10deg)}.decorativeDice.d20{bottom:15%;left:20%;transform:rotate(25deg)}